@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");
:root {
  --font-body: "Lato", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%, 1);
  --whiteLight: hsla(0, 11%, 86%, 0.788);
  --gray: hsla(0, 0%, 85%, 1);
  --grayLight: hsla(0, 0%, 47%, 1);
  --grayDark:hsl(0, 0%, 51%);
  --black: hsl(0, 0%, 0%, 1);
  --darkbackground: hsla(0, 0%, 0%, 0.7);
  --blue: hsl(205, 84%, 55%, 1);
  --blueDark: hsla(200, 67%, 50%, 0.396);
  --colorBlue: #2a9bd4;
  --colorBlack: #000;
  --colorParagraph: #082a3b;
  --bluecontact:hsl(216, 62%, 25%);
  --colorWhite: #fff;
  --bluecontact: hsl(216, 62%, 25%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
